// confirm.js


conifrm_url = () => {
    if (get_element(".confirm")[0]){
        let confirm_btn_list = get_element(".confirm");
        confirm_btn_list.map(cb => {
            cb.addEventListener('click', () => {
                let result = confirm(cb.dataset.confirm_txt);
                if (result) location.href = cb.dataset.url;
            })
        })
    }
}
conifrm_url();



CONFIRM_AJAX_BTN_LIST = get_element(".confirm-ajax");

confirm_ajax = function() {
    let result = confirm(this.dataset.confirm_txt);
    if (result) {
        if (this.dataset.ajax_func == "delete-server")
            execute_server_delete(this.dataset.server_id);

        if (this.dataset.ajax_func == "leave-server")
            execute_server_leave(this.dataset.server_id);

        if (this.dataset.ajax_func == "delete-channel")
            execute_channel_delete(this.dataset.chnl_id);

        if (this.dataset.ajax_func == "delete-message")
            execute_message_delete(this.dataset.msg_id);

        if (this.dataset.ajax_func == "delete-message-img")
            execute_message_file_delete(
                "img",
                this.dataset.msg_id,
                this.dataset.img_name,
                this.parentNode.parentNode
                );

        if (this.dataset.ajax_func == "delete-message-dcmnt")
            execute_message_file_delete(
                "dcmnt",
                this.dataset.msg_id,
                this.dataset.dcmnt_name,
                this.parentNode.parentNode
            );

        if (this.dataset.ajax_func == "leave-account") {
            execute_leave_account();
        }
    }
}

add_confirm_ajax = (elm) => {
    elm.addEventListener('click', confirm_ajax);
    CONFIRM_AJAX_BTN_LIST.push(elm);
}

set_confirm_ajax = () => {
    CONFIRM_AJAX_BTN_LIST.map(cab => {
        cab.addEventListener('click', confirm_ajax)
    });
}

remove_confirm_ajax = () => {
    CONFIRM_AJAX_BTN_LIST.map(cab => {
        cab.removeEventListener('click', confirm_ajax)
    });
}

reset_conifrm_ajax = () => {
    remove_confirm_ajax();
    CONFIRM_AJAX_BTN_LIST = get_element(".confirm-ajax");
    set_confirm_ajax();
}

if (CONFIRM_AJAX_BTN_LIST.length) set_confirm_ajax();