ustify-content: center;\n  height: 28px;\n}\n.p-index__user-action-list {\n  align-items: center;\n  font-size: map-get($font-size, btn-icon-md);\n}\n.p-index__side-user-image {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  overflow: hidden;\n  height: 35px;\n  width: 35px;\n  margin-right: $space-xsm;\n  border-radius: map-get($border-radius-size, full);\n  background-color: map-get($bg-colors, tertiary);\n  > img {\n    object-fit: cover;\n    height: inherit;\n    width: inherit;\n  }\n}\n.p-index__user-action-btn {\n  > i { transition: all 0.15s; }\n  @include hover {\n    > i { scale: 1.4; }\n  }\n}\n\n// 通話セクション\n.p-index__phone-operator {\n  padding: $space-xsm 0;\n  &[data-status=\"off\"] { display: none; }\n}\n// 電話帳\n.p-index__phone-book {\n  height: 220px;\n  .note-for-call {\n    display: flex;\n    justify-content: center;\n    align-items: flex-start;\n    height: 18px;\n    font-size: map-get($font-size, note);\n  }\n  .p-index__callable-user-list {\n    overflow: scroll;\n    height: 202px;\n  }\n}\n.p-index__callable-user-list .user-list {\n  display: flex;\n  align-items: center;\n  border-radius: map-get($border-radius-size, primary);\n  padding: $space-2xsm $space-xsm;\n  cursor: pointer;\n  @include hover {\n    background-color: map-get($bg-colors, tertiary);\n  }\n  .user-list-image {\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    overflow: hidden;\n    height: 30px;\n    width: 30px;\n    margin-right: $space-xsm;\n    border-radius: map-get($border-radius-size, full);\n    background-color: map-get($bg-colors, tertiary);\n    > img {\n      object-fit: cover;\n      height: inherit;\n      width: inherit;\n    }\n  }\n  .user-list-name {\n    width: calc(100% - 30px - $space-xsm);\n    &:has(+ button){\n      width: calc(100% - 30px - $space-xsm - map-get($font-size, btn-icon-md));\n    }\n  }\n  .cancel-call-btn { font-size: map-get($font-size, btn-icon-md); }\n}\n// 通