n  position: absolute;\n  top: $space-md;\n  right: 0;\n  left: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: $space-md;\n  .menu-btn {\n    height: 50px;\n    width: 50px;\n    background-color: map-get($bg-colors, quinary);\n    border-radius: map-get($border-radius-size, full);\n    font-size: map-get($font-size, btn-icon-lg);\n    transition: all 0.2s;\n    @include hover { scale: 1.2; }\n  }\n}\n.p-index__full-call-monitor {\n  display: flex;\n  gap: $space-3xsm;\n  width: 100%;\n}\n.p-index__full-call-monitor .full-user-display {\n  overflow: hidden;\n  position: relative;\n  width: 50%;\n  aspect-ratio: 4 / 3;\n  border-radius: map-get($border-radius-size, primary);\n  img {\n    width: 100%;\n    aspect-ratio: 4 / 3;\n    object-fit: cover;\n  }\n  .user-name {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    position: absolute;\n    bottom: $space-xsm;\n    left: $space-xsm;\n    width: calc(100% - $space-xsm * 2);\n    p, i { background-color: map-get($bg-colors, primary-tp); }\n    p {\n      max-width: 80%;\n      padding: $space-2xsm $space-xsm;\n      border-radius: map-get($border-radius-size, primary);\n    }\n    i {\n      text-align: center;\n      height: map-get($font-size, btn-icon-xlg);\n      width: map-get($font-size, btn-icon-xlg);\n      line-height: map-get($font-size, btn-icon-xlg);\n      border-radius: map-get($border-radius-size, full);\n    }\n  }\n}\n\n\n\n\n// main\n\n.p-index-main {\n  position: relative;\n  width: 100%;\n}\n\n.p-index-main__date-partition {\n  display: flex;\n  align-items: center;\n  margin: $space-xlg 0;\n}\n.p-index-main__date-partition::before, \n.p-index-main__date-partition::after {\n  content: \"\";\n  height: 1px;\n  flex-grow: 1;\n  background-color: #666;\n}\n.p-index-main__date-partition::before {\n  margin-right: 1rem;\n}\n.p-index-main__date-partition::after {\n  margin-left: 1rem;\n}\n\n.p-index-main__output {\n  overflow: scr