tatus=\"on\"]) { display: none !important; }\n\n\n// 該当のデバイス画角に対応してアニメーションを無効にします -----------------------------\n\n//Smartphone(sp)\n.u-non-anim-sp {\n  @include mq(sp) {\n    transition: none !important;\n    transform: none !important;\n  }\n}\n\n//Tablet(tab)\n.u-non-anim-tab-max {\n  @include mq(tab-max) {\n    transition: none !important;\n    transform: none !important;\n  }\n}\n\n.u-non-anim-tab-min {\n  @include mq(tab-min) {\n    transition: none !important;\n    transform: none !important;\n  }\n}\n\n// PC(pc)\n.u-non-anim-pc {\n  @include mq(pc) {\n    transition: none !important;\n    transform: none !important;\n  }\n}\n\n\n// Flex Box ------------------------------------------------------------------\n\n// flex item の縦横を定めるライン\n$min-line-base:  90px;\n$min-line-pc:    $min-line-base;\n$min-line-tab:   $min-line-base * 0.75;\n$min-line-sp:    $min-line-base * 0.5;\n$min-line-list: $min-line-pc, $min-line-tab, $min-line-sp;\n$bp-list: pc, tab, sp;\n\n.msg-img-flex-box {\n  display: flex;\n  flex-wrap: wrap;\n  gap: $space-3xsm;\n}\n\n.msg-img-flex-box .flex-item {\n  display: flex;\n  overflow: hidden;\n  border-radius: map-get($border-radius-size, primary);\n}\n\n.msg-img-flex-box .flex-item img {\n  object-fit: cover;\n  width: 100%;\n}\n\n.msg-img-flex-box .flex-item-wrap--column {\n  display: flex;\n  flex-direction: column;\n  gap: $space-3xsm;\n}\n\n@function devidedWidth($value) {\n  @return calc(100% / $value - $space-3xsm)\n}\n\n@for $i from 1 through 10 {\n  .msg-img-flex-box--#{$i} {\n    @each $ln in $min-line-list {\n      @include mq( nth($bp-list, index($min-line-list,$ln)) ) {\n        @if $i == 1 { width: $ln*4; } @else { width: $ln*6; }\n\n        @if $i == 1 {\n          > .flex-item:nth-child(1) {\n            height: $ln*3;\n            width: devidedWidth(1);\n          }\n        } @else if $i == 2 {\n          @for $ii from 1 through 2 {\n            > .flex-item:nth-child(#{$ii}) {\n              height: $ln*3;\n              width: devidedWidth(2);\n            }\n          }\n        // 3つ画像がある時のみ要素の構成が特殊で、\n        // 1つ目の画像は左に、2・3の画像は縦並びにするwrapに包み右配置する\n        } @else if $i == 3 {\n          > .flex-item:nth-child(1) {\n            height: $ln*4;\n            width: devidedWidth(1.5);\n          }\n          > .flex-item-wrap--column:nth-child(2) {\n            height: $ln*4;\n            width: devidedWidth(3);\n            > .flex-item {\n              height: $ln*2;\n              width: 100%;\n            }\n          }\n        } @else if $i == 4 {\n          @for $ii from 1 through 4 {\n            > .flex-item:nth-child(#{$ii}) {\n              height: $ln*2;\n              width: devidedWidth(2);\n            }\n          }\n        } @else if $i == 5 {\n          @for $ii from 1 through 5 {\n            @if $ii == 1 or $ii == 2 {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*3;\n                width: devidedWidth(2);\n              }\n            } @else {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*2;\n                width: devidedWidth(3);\n              }\n            }\n          }\n        } @else if $i == 6 {\n          @for $ii from 1 through 6 {\n            > .flex-item:nth-child(#{$ii}) {\n              height: $ln*2;\n              width: devidedWidth(3);\n            }\n          }\n        } @else if $i == 7 {\n          @for $ii from 1 through 7 {\n            @if $ii == 1 {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*3;\n                width: devidedWidth(1);\n              }\n            } @else {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*2;\n                width: devidedWidth(3);\n              }\n            }\n          }\n        } @else if $i == 8 {\n          @for $ii from 1 through 8 {\n            @if $ii == 1 or $ii == 2 {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*3;\n                width: devidedWidth(2);\n              }\n            } @else {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*2;\n                width: devidedWidth(3);\n              }\n            }\n          }\n        } @else if $i == 9 {\n          @for $ii from 1 through 9 {\n            > .flex-item:nth-child(#{$ii}) {\n              height: $ln*2;\n              width: devidedWidth(3);\n            }\n          }\n        } @else if $i == 10 {\n          @for $ii from 1 through 10 {\n            @if $ii == 1 {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*3;\n                width: devidedWidth(1);\n              }\n            } @else {\n              > .flex-item:nth-child(#{$ii}) {\n                height: $ln*2;\n                width: devidedWidth(3);\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n\n// その他 ----------------------------------------------------------------------\n\n// マウスカーソルの形をポインター型にします\n.u-pointer { cursor: pointer; }\n\n// 画面更新後に起こる、アニメーションの誤挙動を制御します(src/js/preloadm.jsありき)\n.u-preload { display: none; }\n\n// アイコン等を含むテキストの並びを並行にします\n.u-txt-opt {\n  display: flex;\n  align-items: center;\n}\n\n// active状態の項目の背景を設定します\n.u-active-item {\n  background-color: map-get($bg-colors, active) !important;\n}\n\n.u-dp-flx { display: flex; }\n.u-dp-flx--btwn {\n  display: flex;\n  justify-content: end;\n}\n.u-dp-flx--center {\n  display: flex;\n  justify-content: center;\n}\n\n.u-dp-inln-blck { display: inline-block; }\n\n.u-txt-center { text-align: center; }\n\n.u-string-shortener {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.u-f-w--bold { font-weight: bold; }\n\n.u-bg--alert { background-color: map-get($bg-colors, alert) !important; }\n\n.u-mrgn-center--hrzn {\n  margin-right: auto;\n  margin-left: auto;\n}"],"sourceRoot":""}]);
